import {ChangeDetectionStrategy, Component, inject, signal} from '@angular/core';
import {FlexColumnComponent} from '../../../../shared/view/layout/flex-column.component';
import {RouterLink} from '@angular/router';

import {TitleComponent} from '../../../../shared/view/layout/title.component';
import {TimeAgoPipe} from '../../../../shared/view/pipes/time-ago.pipe';
import {ProductsRepository} from '../../../products/service/products.repository';
import {Product} from '../../../products/service/Product';
import {FlexRowComponent} from '../../../../shared/view/layout/flex-row.component';
import {PullDirective} from '../../../../shared/view/directives/pull.directive';
import {TranslatePipe} from '@ngx-translate/core';
import {productLabelFactoryProvider} from '../../../../shared/factories/entity-labels/product.label.factory';

@Component({
  selector: 'lg-last-edited-products',
  template: `
    <lg-flex-column size="medium">
      <lg-title [level]="4">{{ 'main.last-products'|translate }}</lg-title>

      <div class="last-edited-recipes">
        <lg-flex-column [size]="'medium'">
          @for (item of products(); track item.product.uuid) {
            <lg-flex-row [size]="'medium'" [mobileMode]="true">
              <a [routerLink]="['/products/edit/', item.product.uuid]"
                 class="last-edited-product">
                {{ productLabelFactory(item.product) }}
              </a>

              <small class="text-muted text-right text-cursive" lgPull>
                {{ (item?.updatedAt) | timeAgo }}
              </small>
            </lg-flex-row>
          } @empty {
            <div class="last-edited-recipe-name">
              {{ 'no-products'|translate }}
            </div>
          }
        </lg-flex-column>
      </div>
    </lg-flex-column>
  `,
  styles: [
    `
      :host {
        display: flex;
        flex-direction: column;
        width: 100%;
      }
    `,
  ],
  imports: [
    FlexColumnComponent,
    RouterLink,
    TitleComponent,
    TimeAgoPipe,
    FlexRowComponent,
    PullDirective,
    TranslatePipe
  ],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class LastEditedProductsComponent {
  constructor(
    private _productsRepository: ProductsRepository,
  ) {
  }

  products = signal<{
    product: Product
    updatedAt: number
    count: number
  }[]>([]);
  protected readonly productLabelFactory = inject(productLabelFactoryProvider);

  ngOnInit() {
    this._productsRepository.getLastProducts()
      .then(products => {
        this.products.set(products);
      });
  }
}
